import { useEffect } from "react";

export default function useLinkCursor() {
  useEffect(() => {
    const navLinks = document.querySelectorAll(
      "a, button, .intro-details strong"
    );
    const cursor = document.getElementById("cursor");
    const follow = document.getElementById("follow");
    const addTransformer = () => {
      if (cursor) {
        cursor.classList.add("link");
      }
      if (follow) {
        follow.classList.add("link");
      }
    };
    const removeTransformer = () => {
      if (cursor) {
        cursor.classList.remove("link");
      }
      if (follow) {
        follow.classList.remove("link");
      }
    };
    for (let i = 0; i < navLinks.length; i++) {
      navLinks[i].addEventListener("mouseenter", addTransformer);
      navLinks[i].addEventListener("mouseleave", removeTransformer);
    }
  }, []);
}
